// Pipelines API v1
// GET /api/v1/pipelines - List pipelines
// POST /api/v1/pipelines - Create pipeline

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

// GET - List pipelines with stages
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const includeStages = searchParams.get('includeStages') !== 'false'
    const includeDeals = searchParams.get('includeDeals') === 'true'
    
    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }
    
    // Get pipelines
    const { data: pipelines, error } = await supabaseAdmin
      .from('Pipeline')
      .select('*')
      .eq('subAccountId', subAccountId)
      .eq('isActive', true)
      .order('displayOrder', { ascending: true })
    
    if (error) throw error
    
    // Get stages for each pipeline if requested
    if (includeStages && pipelines && pipelines.length > 0) {
      const pipelineIds = pipelines.map(p => p.id)
      
      const { data: stages, error: stagesError } = await supabaseAdmin
        .from('PipelineStage')
        .select('*')
        .in('pipelineId', pipelineIds)
        .eq('isActive', true)
        .order('displayOrder', { ascending: true })
      
      if (stagesError) throw stagesError
      
      // Group stages by pipeline
      const stagesByPipeline = stages?.reduce((acc: any, stage: any) => {
        if (!acc[stage.pipelineId]) acc[stage.pipelineId] = []
        acc[stage.pipelineId].push(stage)
        return acc
      }, {}) || {}
      
      // Attach stages to pipelines
      pipelines.forEach((pipeline: any) => {
        pipeline.stages = stagesByPipeline[pipeline.id] || []
      })
      
      // Get deals if requested
      if (includeDeals) {
        const { data: deals, error: dealsError } = await supabaseAdmin
          .from('Deal')
          .select('*')
          .in('pipelineId', pipelineIds)
          .neq('status', 'abandoned')
          .order('stagePosition', { ascending: true })
        
        if (dealsError) throw dealsError
        
        // Group deals by stage
        const dealsByStage = deals?.reduce((acc: any, deal: any) => {
          if (!acc[deal.stageId]) acc[deal.stageId] = []
          acc[deal.stageId].push(deal)
          return acc
        }, {}) || {}
        
        // Attach deals to stages
        pipelines.forEach((pipeline: any) => {
          pipeline.stages.forEach((stage: any) => {
            stage.deals = dealsByStage[stage.id] || []
          })
        })
      }
    }
    
    return NextResponse.json({
      success: true,
      data: pipelines || [],
    })
  } catch (error: any) {
    console.error('GET /api/v1/pipelines error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch pipelines' },
      { status: 500 }
    )
  }
}

// POST - Create pipeline with default stages
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const {
      subAccountId,
      name,
      description,
      icon = 'ðŸŽ¯',
      color = '#3B82F6',
      isDefault = false,
      createDefaultStages = true,
    } = body
    
    if (!subAccountId || !name) {
      return NextResponse.json(
        { success: false, error: 'subAccountId and name are required' },
        { status: 400 }
      )
    }
    
    // Check if pipeline name already exists
    const { data: existing } = await supabaseAdmin
      .from('Pipeline')
      .select('id')
      .eq('subAccountId', subAccountId)
      .eq('name', name)
      .single()
    
    if (existing) {
      return NextResponse.json(
        { success: false, error: `Pipeline '${name}' already exists` },
        { status: 409 }
      )
    }
    
    // Get max display order
    const { data: maxOrderResult } = await supabaseAdmin
      .from('Pipeline')
      .select('displayOrder')
      .eq('subAccountId', subAccountId)
      .order('displayOrder', { ascending: false })
      .limit(1)
      .single()
    
    const displayOrder = (maxOrderResult?.displayOrder || 0) + 1
    
    // If setting as default, unset other defaults
    if (isDefault) {
      await supabaseAdmin
        .from('Pipeline')
        .update({ isDefault: false })
        .eq('subAccountId', subAccountId)
    }
    
    // Create pipeline
    const { data: pipeline, error } = await supabaseAdmin
      .from('Pipeline')
      .insert({
        subAccountId,
        name,
        description,
        icon,
        color,
        isDefault,
        displayOrder,
        isActive: true,
      })
      .select()
      .single()
    
    if (error) throw error
    
    // Create default stages if requested
    if (createDefaultStages && pipeline) {
      const defaultStages = [
        { name: 'New Lead', color: '#9CA3AF', stageType: 'open', displayOrder: 0, winProbability: 10 },
        { name: 'Qualified', color: '#3B82F6', stageType: 'open', displayOrder: 1, winProbability: 25 },
        { name: 'Proposal', color: '#F59E0B', stageType: 'open', displayOrder: 2, winProbability: 50 },
        { name: 'Negotiation', color: '#8B5CF6', stageType: 'open', displayOrder: 3, winProbability: 75 },
        { name: 'Won', color: '#10B981', stageType: 'won', displayOrder: 4, winProbability: 100 },
        { name: 'Lost', color: '#EF4444', stageType: 'lost', displayOrder: 5, winProbability: 0 },
      ]
      
      const stagesToInsert = defaultStages.map(stage => ({
        pipelineId: pipeline.id,
        ...stage,
        isActive: true,
      }))
      
      const { data: stages, error: stagesError } = await supabaseAdmin
        .from('PipelineStage')
        .insert(stagesToInsert)
        .select()
      
      if (stagesError) {
        console.error('Error creating default stages:', stagesError)
      } else {
        pipeline.stages = stages
      }
    }
    
    return NextResponse.json({
      success: true,
      data: { pipeline },
    }, { status: 201 })
  } catch (error: any) {
    console.error('POST /api/v1/pipelines error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to create pipeline' },
      { status: 500 }
    )
  }
}
